'use client'

import React, { useState } from 'react';
import { ShoppingBag, Heart, User, Home, Package, LayoutDashboard, Menu, X, Mail } from 'lucide-react';
import Link from 'next/link';
import { MobileNavLink, NavLink } from './NavLink';

export const Navbar: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="bg-white shadow-md fixed w-full top-0 z-50">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          <Link href="/" className="flex-shrink-0 flex items-center">
            <ShoppingBag className="h-8 w-8 text-rose-gold" />
            <span className="ml-2 text-xl font-serif font-bold">LuxeBags</span>
          </Link>
          
          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            <NavLink to="/" icon={<Home size={20} />} label="Home" />
            <NavLink to="/shop" icon={<Package size={20} />} label="Shop" />
            <NavLink to="/dashboard" icon={<LayoutDashboard size={20} />} label="Dashboard" />
            <NavLink to="/wishlist" icon={<Heart size={20} />} label="Wishlist" />
            <NavLink to="/cart" icon={<ShoppingBag size={20} />} label="Cart" />
            <NavLink to="/contact" icon={<Mail size={20} />} label="Contact" />
            <NavLink to="/dashboard" icon={<User size={20} />} label="Account" />
          </div>

          {/* Mobile Menu Button */}
          <button 
            className="md:hidden p-2"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label={isMenuOpen ? 'Close menu' : 'Open menu'}
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isMenuOpen && (
          <div className="md:hidden">
            <div className="px-2 pt-2 pb-3 space-y-1">
              <MobileNavLink to="/" icon={<Home size={20} />} label="Home" />
              <MobileNavLink to="/shop" icon={<Package size={20} />} label="Shop" />
              <MobileNavLink to="/dashboard" icon={<LayoutDashboard size={20} />} label="Dashboard" />
              <MobileNavLink to="/wishlist" icon={<Heart size={20} />} label="Wishlist" />
              <MobileNavLink to="/cart" icon={<ShoppingBag size={20} />} label="Cart" />
              <MobileNavLink to="/contact" icon={<Mail size={20} />} label="Contact" />
              <MobileNavLink to="/dashboard" icon={<User size={20} />} label="Account" />
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};